import React from 'react';
import { useEffect } from 'react';
import Innerbanner from '../Common/Banners/Innerbanner';
import Contactform from './Contactform';
import InnerImageBanner from '../Common/Banners/InnerImageBanner';
import './Contact.css'




const Contact = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const  data ={
    pageTitle: "Contact",
    pageHeading: "How can we help you?",
    pageContent: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industrys standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.",
  };

  const dataNew ={ 
    classTitle: "banner-contact"
  };

  document.title = "Contact";

  return (
    <div className='contact'>
      <div className='container'>
            <Innerbanner data={data} />
            
       </div>

       <InnerImageBanner  data={dataNew}/>
       <Contactform />
    </div>
  )
}

export default Contact